import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CallRouteComponent } from './call-route/call-route.component';
import { DateTimeManageComponent } from './date-time-manage/date-time-manage.component';
import { SipGatewayComponent } from './sip-gateway/sip-gateway.component';
import { SipServicesComponent } from './sip-services/sip-services.component';
import { SipUserComponent } from './sip-user/sip-user.component';
import { UserGroupComponent } from './user-group/user-group.component';
const routes: Routes = [
  { path: 'sip-services', component: SipServicesComponent },
  { path: 'sip-user', component: SipUserComponent },
  { path: 'user-group', component: UserGroupComponent },
  { path: 'sip-gateway', component: SipGatewayComponent },
  { path: 'call-route', component: CallRouteComponent },
  { path: 'date-time-manage', component: DateTimeManageComponent },
];
@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class SmartSipPbxRoutingModule {}
