import { Component, OnInit, ChangeDetectionStrategy, ChangeDetectorRef } from '@angular/core';
import { PageEvent } from '@angular/material/paginator';
import { MtxGridColumn } from '@ng-matero/extensions';
import { TablesRemoteDataService } from '../date-time-manage/remote-data.service';
@Component({
  selector: 'app-date-time-manage',
  templateUrl: './date-time-manage.component.html',
  styleUrls: ['./date-time-manage.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [TablesRemoteDataService],
})
export class DateTimeManageComponent implements OnInit {
  
  
  columns: MtxGridColumn[] = [
    {
      header: 'NAME',
      field: 'name',
      formatter: (data: any) => `<a href="${data.html_url}" target="_blank">${data.name}</a>`,
    },
    { header: 'DATE OPTIONS', field: 'date options' },
    { header: 'WEEK DAYS', field: 'week days', },
    { header: 'TIME OPTIONS', field: 'time options' },
    { header: 'ACTON', field: 'action', type: 'number' },
    
   ];
  list = [];
  total = 0;
  isLoading = false;

  query = {
    q: 'user:nzbin',
    sort: 'stars',
    order: 'desc',
    page: 0,
    per_page: 10,
  };
  dialog: any;

  get params() {
    const p = Object.assign({}, this.query);
    p.page += 1;
    return p;
  }

  constructor(private remoteSrv: TablesRemoteDataService, private cdr: ChangeDetectorRef ) {}

 
  ngOnInit() {
    // this.getData();

  }

  getData() {
    this.isLoading = true;

    this.remoteSrv.getData(this.params).subscribe(
      res => {
        this.list = res.items;
        this.total = res.total_count;
        this.isLoading = false;
        this.cdr.detectChanges();
      },
      () => {
        this.isLoading = false;
        this.cdr.detectChanges();
      },
      () => {
        this.isLoading = false;
        this.cdr.detectChanges();
      }
    );
  }

  getNextPage(e: PageEvent) {
    this.query.page = e.pageIndex;
    this.query.per_page = e.pageSize;
    this.getData();
  }

  search() {
    this.query.page = 0;
    this.getData();
  }
}



